body {
  font-family: 'Montserrat', sans-serif;
  box-sizing: border-box;
  position: relative;
  font-weight: 500;
  font-size: 16px;
}

.container {
  box-sizing: border-box;
}

@media only screen and (min-width: 1200px) {
  .container {
    max-width: 1110px !important;
  }
}

a {
  text-decoration: none;
  transition: all 0.3s;
}

input[type="submit"] {
  appearance: none;
  -webkit-appearance: none;
}

textarea {
  resize: none;
}

@media only screen and (max-width: 767px) {
  .hidden-xs {
    display: none !important;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .hidden-sm {
    display: none !important;
  }
}

@media only screen and (min-width: 1024px) {
  .hidden-md {
    display: none !important;
  }
}

i.icon-play {
  width: 50px;
  height: 50px;
  display: flex;
  position: relative;
  background: #fff;
  border-radius: 50%;
}

i.icon-play::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 60%;
  transform: translate(-50%, -50%);
  border: 7px solid transparent;
  border-left: 12px solid #53BF51;
}

.button-green {
  background: linear-gradient(112.31deg, #53BF51 0%, #8BDC8A 100%);
  box-shadow: 0px 5px 30px rgba(198, 207, 217, 0.3);
  border-radius: 40px;
  display: block;
  padding: 15px 60px;
  font-size: 16px;
  font-weight: bold;
  color: #FFFFFF;
}

.button-green:hover {
  box-shadow: 0px 5px 10px 0px rgba(198, 207, 217, 0.7);
}

.button-transparent {
  font-weight: bold;
  font-size: 16px;
  color: #53BF51;
  padding: 13px 40px;
  border: 2px solid #53BF51;
  border-radius: 30px;
  display: inline-flex;
}

.button-transparent:hover {
  background: #53BF51;
  color: #fff;
}

@media only screen and (max-width: 767px) {
  .button-transparent {
    padding: 10px 25px;
  }
}

.logo {
  font-weight: bold;
  font-size: 36px;
  color: #53BF51;
}

.logo span {
  color: #000;
}

@media only screen and (max-width: 767px) {
  .logo {
    font-size: 18px;
  }
}

.header {
  padding: 38px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transition: all 0.5s;
  z-index: 9;
}

@media only screen and (max-width: 992px) {
  .header {
    padding: 15px 0;
  }
}

.header.scroll {
  background: #fff;
  padding: 16px 0;
  box-shadow: 0px 5px 30px rgba(198, 207, 217, 0.3);
}

.header .menu-open {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translate(0, -50%);
  width: 20px;
  height: 16px;
  display: none;
  z-index: 99;
  transition: all 0.3s;
}

@media only screen and (max-width: 992px) {
  .header .menu-open {
    display: block;
  }
}

.header .menu-open span {
  width: 20px;
  height: 2px;
  background: #000000;
  display: block;
  transition: all 0.3s;
}

.header .menu-open span:before, .header .menu-open span:after {
  content: '';
  width: 20px;
  height: 2px;
  background: #000000;
  position: absolute;
  left: 0;
  transition: all 0.3s;
}

.header .menu-open span:before {
  top: 7px;
}

.header .menu-open span:after {
  bottom: 0px;
}

.header.active {
  background: #fff;
  box-shadow: 0px 5px 30px rgba(198, 207, 217, 0.3);
}

.header.active .menu-open {
  height: 20px;
  border-radius: 50%;
  border: 2px solid #000;
}

.header.active .menu-open span {
  background: none;
}

.header.active .menu-open span::before, .header.active .menu-open span::after {
  width: 10px;
  left: 3px;
}

.header.active .menu-open span::before {
  transform: rotate(45deg);
  top: 7px;
}

.header.active .menu-open span::after {
  transform: rotate(-45deg);
  bottom: 7px;
}

.header.active .button-transparent {
  opacity: 0;
  visibility: hidden;
}

.header__row {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

@media only screen and (max-width: 992px) {
  .header__row {
    padding-right: 50px;
  }
}

@media only screen and (max-width: 374px) {
  .header__row {
    padding-right: 30px;
  }
}

.header__nav {
  display: flex;
}

@media only screen and (max-width: 992px) {
  .header__nav {
    display: none;
  }
}

.header__nav li {
  margin-left: 40px;
}

.header__nav a {
  font-weight: 500;
  font-size: 16px;
  color: #050606;
  padding: 10px 0;
  display: inline-block;
}

.header__nav a:hover {
  text-decoration: underline;
}

.header .button-transparent {
  margin-left: 40px;
}

@media only screen and (max-width: 767px) {
  .header .button-transparent {
    margin: 0;
  }
}

.mobile-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #53BF51;
  z-index: 8;
  padding-top: 130px;
  padding-bottom: 110px;
  overflow: auto;
  display: none;
}

.mobile-menu__menu {
  padding-bottom: 80px;
}

.mobile-menu__menu li {
  display: flex;
  justify-content: center;
  align-items: center;
}

.mobile-menu__menu li a {
  font-weight: bold;
  font-size: 36px;
  color: #fff;
  margin-bottom: 60px;
}

.mobile-menu b {
  font-weight: normal;
  font-size: 24px;
  display: block;
  text-align: center;
  line-height: 1.6;
  color: #fff;
}

.mobile-menu b a {
  color: #fff;
  display: block;
  margin-bottom: 60px;
}

.mobile-menu__social {
  display: flex;
  justify-content: center;
  padding-top: 25px;
}

.mobile-menu__social a {
  font-size: 24px;
  padding: 0 24px;
}

.mobile-menu__social a svg {
  fill: #fff;
}

.footer {
  height: 100px;
  background: #000000;
}

.intro {
  height: 995px;
  display: flex;
  align-items: center;
  background: #F1FCF7;
  position: relative;
  overflow: hidden;
}

@media only screen and (max-width: 992px) {
  .intro {
    height: auto;
    padding-top: 110px;
    padding-bottom: 40px;
  }
}

.intro__text {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  z-index: 7;
}

@media only screen and (max-width: 992px) {
  .intro__text {
    padding-bottom: 20px;
  }
}

.intro__text h1 {
  font-weight: bold;
  font-size: 64px;
  line-height: 1.3;
  margin-bottom: 30px;
}

@media only screen and (max-width: 767px) {
  .intro__text h1 {
    font-size: 36px;
  }
}

.intro__text p {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  max-width: 445px;
  width: 100%;
  color: #8D8D8C;
  margin-bottom: 60px;
}

@media only screen and (max-width: 767px) {
  .intro__text p {
    margin-bottom: 30px;
  }
}

.intro__text div {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 1200px) {
  .intro__text div {
    flex-direction: column;
    align-items: flex-start;
  }
}

.intro__text div a:last-child {
  font-weight: bold;
  font-size: 16px;
  display: flex;
  align-items: center;
  color: #53BF51;
  margin-left: 30px;
}

@media only screen and (max-width: 1200px) {
  .intro__text div a:last-child {
    margin: 30px 0;
  }
}

.intro__text div a:last-child i.icon-play {
  margin-right: 25px;
  transition: all 0.3s;
  box-shadow: 0px 5px 30px rgba(198, 207, 217, 0.3);
}

.intro__text div a:last-child:hover {
  text-decoration: underline;
}

.intro__text div a:last-child:hover i {
  transform: scale(1.1);
  box-shadow: 0px 10px 20px rgba(198, 207, 217, 0.3);
}

.intro__parallax {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  left: -50px;
}

@media only screen and (max-width: 992px) {
  .intro__parallax {
    left: 0;
    height: 300px;
  }
}

.intro__parallax div {
  position: absolute;
}

.intro__parallax #basket {
  z-index: 7;
}

.intro__parallax #basket img {
  width: 350px;
}

@media only screen and (max-width: 992px) {
  .intro__parallax #basket img {
    width: 147px;
  }
}

.intro__parallax #foliage {
  z-index: 6;
}

.intro__parallax #foliage img {
  width: 595px;
}

@media only screen and (max-width: 992px) {
  .intro__parallax #foliage img {
    width: 250px;
  }
}

.intro__parallax #overcast {
  z-index: 5;
}

.intro__parallax #overcast img {
  width: 830px;
}

@media only screen and (max-width: 992px) {
  .intro__parallax #overcast img {
    width: 390px;
  }
}
